html,
body {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
}

#dynamicpockertable {
	height: 100%;
	width: 100%;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
	gap: 0px 0px;
}

.table-wrapper {
	grid-area: 2 / 2 / 5 / 7;
}

.table-wrapper .table {
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
	background: #095d2a;
	border-radius: 120px;
	border: 10px solid #c18561;
	-webkit-box-shadow: inset 0px 6px 13px 0px rgba(50, 50, 50, 0.75);
	-moz-box-shadow: inset 0px 6px 13px 0px rgba(50, 50, 50, 0.75);
	box-shadow: inset 0px 6px 13px 0px rgba(50, 50, 50, 0.75);
}

.message-box {
	grid-area: 3 / 3 / 4 / 6;
	border: 3px solid rgba(255, 255, 255, 0.4);
	box-sizing: border-box;
	border-radius: 4px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.players-grid {
	display: grid;
	width: 100%;
	grid-area: 1/1/-1/-1;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
	transition: all ease 0.2s;
	grid-template-rows: 1fr 1fr 1fr 1fr 1fr;

	&_2-players {
		grid-template-areas:
			". . . . . . ."
			". . . . . . ."
			"p1 . . . . . p2"
			". . . . . . ."
			". . . . . . .";
	}
	&_3-players {
		grid-template-areas:
			". . . . . . ."
			". . . . . . ."
			"p1 . . . . . p2"
			". . . . . . ."
			". . . p3 . . .";
	}
	&_4-players {
		grid-template-areas:
			". . . p4 . . ."
			". . . . . . ."
			"p1 . . . . . p2"
			". . . . . . ."
			". . . p3 . . .";
	}
	&_5-players {
		grid-template-areas:
			". . p4 . p5 . ."
			". . . . . . ."
			"p1 . . . . . ."
			". . . . . . ."
			". . p2 . p3 . .";
	}
	&_6-players {
		grid-template-areas:
			". . p4 . p5 . ."
			". . . . . . ."
			"p1 . . . . . p2"
			". . . . . . ."
			". . p6 . p3 . .";
	}
	&_7-players {
		grid-template-areas:
			"p3 . . . . . p4"
			". . . . . . ."
			"p1 . . . . . p2"
			". . . . . . ."
			"p6 . . p7 . . p5";
	}
	&_8-players {
		grid-template-areas:
			"p3 . . p8 . . p4"
			". . . . . . ."
			"p1 . . . . . p2"
			". . . . . . ."
			"p6 . . p7 . . p5";
	}
	&_9-players {
		grid-template-areas:
			"p2 . p3 . p4 . p5"
			". . . . . . ."
			"p1 . . . . . ."
			". . . . . . ."
			"p6 . p7 . p8 . p9";
	}
	&_10-players {
		grid-template-areas:
			"p2 . p3 . p4 . p5"
			". . . . . . ."
			"p1 . . . . . p10"
			". . . . . . ."
			"p6 . p7 . p8 . p9";
	}
}

.player {
	background: #000;
	width: 100%;
	height: 100%;
	transition: all ease 0.2s;

	&:nth-of-type(1) {
		grid-area: p1;
	}
	&:nth-of-type(2) {
		grid-area: p2;
	}
	&:nth-of-type(3) {
		grid-area: p3;
	}
	&:nth-of-type(4) {
		grid-area: p4;
	}
	&:nth-of-type(5) {
		grid-area: p5;
	}
	&:nth-of-type(6) {
		grid-area: p6;
	}
	&:nth-of-type(7) {
		grid-area: p7;
	}
	&:nth-of-type(8) {
		grid-area: p8;
	}
	&:nth-of-type(9) {
		grid-area: p9;
	}
	&:nth-of-type(10) {
		grid-area: p10;
	}
}
